import React, { Component } from 'react';

import CircularMigrationPlot from 'circular-migration-plot/dist/circular-migration-plot';
import './migration-flow.scss'

export default class MigrationFlow extends Component {
  render() {
    return <div id="migration-flow">
        <div id="timeline"></div>
        <div id="chart"></div>
      </div>;
  }

  componentDidMount() {
    CircularMigrationPlot({
      data: 'data/circular_flow_plot.json',
      chart: {
          element: '#chart',
          width: 1500,
          margin: 100,
          mylayout: {
              threshold: 1,
              labelThreshold: 1
          }
      },
      timeline: '#timeline',
    });
  }
}
