// https://observablehq.com/d/888ac15da2660a31@316
export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function(md){return(
md`# Brushable Scatterplot Matrix

This [scatterplot matrix](/@d3/scatterplot-matrix) allows brushing to select data points in one cell, and highlight them across all other cells.`
)});
  main.variable(observer("scatter")).define("scatter", ["d3","padding","width","xAxis","yAxis","columns","size","data","x","y","classes","z","brush"], function(d3,padding,width,xAxis,yAxis,columns,size,data,x,y,classes,z,brush)
{
  const svg = d3.create("svg")
      .attr("viewBox", [-padding, 0, width + 450, width]);

  svg.append("style")
      .text(`circle.hidden { fill: #000; fill-opacity: 1; r: 1px; }`);

  svg.append("g")
      .call(xAxis);

  svg.append("g")
      .call(yAxis);

  const cell = svg.append("g")
    .selectAll("g")
    .data(d3.cross(d3.range(columns.length), d3.range(columns.length)))
    .join("g")
      .attr("transform", ([i, j]) => `translate(${i * size},${j * size})`);

  cell.append("rect")
      .attr("fill", "none")
      .attr("stroke", "#aaa")
      .attr("x", padding / 2 + 0.5)
      .attr("y", padding / 2 + 0.5)
      .attr("width", size - padding)
      .attr("height", size - padding);

  cell.each(function([i, j]) {
    d3.select(this).selectAll("circle")
      .data(data)
      .join("circle")
        .attr("cx", d => x[i](d[columns[i]]))
        .attr("cy", d => y[j](d[columns[j]]))
        .attr("class", d => classes[d.causa]);
  });

  const circle = cell.selectAll("circle")
      .attr("r", 3.5)
      .attr("fill-opacity", 0.7)
      .attr("fill", d => z(d.causa));

  cell.call(brush, circle);

  svg.append("g")
      .style("font", "bold 10px sans-serif")
      .style("pointer-events", "none")
    .selectAll("text")
    .data(columns)
    .join("text")
      .attr("transform", (d, i) => `translate(${i * size},${i * size})`)
      .attr("x", padding)
      .attr("y", padding)
      .attr("dy", ".71em")
      .text(d => d);

  const keys = [
    'Desistiu do curso anterior',
    'Formou-se em um curso antes de ingressar no próximo',
    'Mudou de curso pelos trâmites da UFC',
    'Limpeza de histórico',
    'Formou-se em um curso que havia desistido',
    'Está fazendo hoje um curso que havia desistido',
  ]

  // Add one dot in the legend for each name.
  svg.selectAll("mydots")
  .data(keys)
  .enter()
  .append("circle")
    .attr("cx", width)
    .attr("cy", (d,i) => 10 + i*25)
    .attr("r", 7)
    .style("fill", function(d){ return z(d)})
  
  // Add one dot in the legend for each name.
  svg.selectAll("mylabels")
    .data(keys)
    .enter()
    .append("text")
      .attr("x", width + 10)
      .attr("y", function(d,i){ return 10 + i*25})
    .style("fill", function(d){ return z(d)})
    .text(function(d){ return d})
    .attr("text-anchor", "left")
    .style("alignment-baseline", "middle")
    .style("width", "200")
    //.on("mouseenter", (e) => focusOn(e, cell))
    //.on("mouseout", scatterTooltipHide)
  
  return svg.node();
}
);
  main.variable(observer("classes")).define("classes", function()
{
  return {
    'Desistiu do curso anterior': 'd1',
    'Formou-se em um curso antes de ingressar no próximo': 'd2',
    'Mudou de curso pelos trâmites da UFC': 'd3',
    'Limpeza de histórico': 'd4',
    'Formou-se em um curso que havia desistido': 'd5',
    'Está fazendo hoje um curso que havia desistido': 'd6',
  }
}
);
  main.variable(observer("focusOn")).define("focusOn", ["classes"], function(classes){return(
(e, cell) => cell.selectAll(`${classes[e]}`).attr("fill-opacity", 0.2)
)});
  main.variable(observer("brush")).define("brush", ["d3","padding","size","x","columns","y"], function(d3,padding,size,x,columns,y){return(
function brush(cell, circle) {
  const brush = d3.brush()
      .extent([[padding / 2, padding / 2], [size - padding / 2, size - padding / 2]])
      .on("start", brushstarted)
      .on("brush", brushed)
      .on("end", brushended);

  cell.call(brush);

  let brushCell;

  // Clear the previously-active brush, if any.
  function brushstarted() {
    console.log("brushstarted", brushCell === this);
    if (brushCell !== this) {
      d3.select(brushCell).call(brush.move, null);
      brushCell = this;
    }
  }

  // Highlight the selected circles.
  function brushed([i, j]) {
    if (d3.event.selection === null) return;
    const [[x0, y0], [x1, y1]] = d3.event.selection; 
    circle.classed("hidden", d => {
      return x0 > x[i](d[columns[i]])
          || x1 < x[i](d[columns[i]])
          || y0 > y[j](d[columns[j]])
          || y1 < y[j](d[columns[j]]);
    });
  }

  // If the brush is empty, select all circles.
  function brushended() {
    if (d3.event.selection !== null) return;
    circle.classed("hidden", false);
  }
}
)});
  main.variable(observer("x")).define("x", ["columns","d3","data","padding","size"], function(columns,d3,data,padding,size){return(
columns.map(c => d3.scaleLinear()
    .domain(d3.extent(data, d => d[c]))
    .rangeRound([padding / 2, size - padding / 2]))
)});
  main.variable(observer("y")).define("y", ["x","size","padding"], function(x,size,padding){return(
x.map(x => x.copy().range([size - padding / 2, padding / 2]))
)});
  main.variable(observer("z")).define("z", ["d3","data"], function(d3,data){return(
d3.scaleOrdinal()
    .domain(data.map(d => d.causa))
    .range(d3.schemeCategory10)
)});
  main.variable(observer()).define(["z"], function(z){return(
z(3)
)});
  main.variable(observer("xAxis")).define("xAxis", ["d3","size","columns","x"], function(d3,size,columns,x)
{
  const axis = d3.axisBottom()
      .ticks(6)
      .tickSize(size * columns.length);
  return g => g.selectAll("g").data(x).join("g")
      .attr("transform", (d, i) => `translate(${i * size},0)`)
      .each(function(d) { return d3.select(this).call(axis.scale(d)); })
      .call(g => g.select(".domain").remove())
      .call(g => g.selectAll(".tick line").attr("stroke", "#ddd"));
}
);
  main.variable(observer("yAxis")).define("yAxis", ["d3","size","columns","y"], function(d3,size,columns,y)
{
  const axis = d3.axisLeft()
      .ticks(6)
      .tickSize(-size * columns.length);
  return g => g.selectAll("g").data(y).join("g")
      .attr("transform", (d, i) => `translate(0,${i * size})`)
      .each(function(d) { return d3.select(this).call(axis.scale(d)); })
      .call(g => g.select(".domain").remove())
      .call(g => g.selectAll(".tick line").attr("stroke", "#ddd"));
}
);
  main.variable(observer("data")).define("data", ["d3"], async function(d3){return(
await d3.csv('https://gist.githubusercontent.com/SrMouraSilva/ed2107e36a8f7bf65c209870f5694214/raw/1dae7d15423b596cf187de2f5864633f1d2b4510/scatterplot.csv')
)});
  main.variable(observer("columns")).define("columns", ["data"], function(data){return(
data.columns.filter(d => d !== "causa")
)});
  main.variable(observer("width")).define("width", function(){return(
954
)});
  main.variable(observer("size")).define("size", ["width","columns","padding"], function(width,columns,padding){return(
(width - (columns.length + 1) * padding) / columns.length + padding
)});
  main.variable(observer("padding")).define("padding", function(){return(
20
)});
  main.variable(observer("d3")).define("d3", ["require"], function(require){return(
require("d3@5")
)});
  return main;
}
