import React, { Component } from 'react';

import {Runtime, Inspector} from "@observablehq/runtime";
import notebook from "d81b595ec1662ba7";

import './sankey-nivel.css'

export default class MigrationFlow extends Component {
  animationRef: React.RefObject<unknown>;

  constructor(props: any) {
    super(props);
    this.animationRef = React.createRef();
  }

  render() {
    return (
      <div ref={this.animationRef as any} id="sankey"></div>
    );
  }

  componentDidMount() {
    const runtime = new Runtime();

    runtime.module(notebook, (name: any) => {
      if (name === "chart") {
        return new Inspector(this.animationRef.current);
      }
    });
  }
}

